@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');

@import url('https://fonts.googleapis.com/css?family=Muli&display=swap');

:root {
  --white: #fcfcfc;
  --black: #252525;

  --purple: #cf07fb;
  --purple-gradient: linear-gradient(to top, #9733ee, #cf07fb);

  /*--green: #49e691; */
  --green: #36e77e;
  --green-gradient: linear-gradient(to top, #061700, #49e691);
}

.container {
  margin: 0 auto;
  color: var(--white);
  font-family: 'Muli', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-y: hidden;
}

.title {
  font-size: 48px;
  text-align: left;
  margin: 0;
  margin-top: 30px;
  padding: 0 20px;
}

.box {
  margin: 20px;
  background: linear-gradient(60deg, rgba(84, 58, 183, 1) 0%, rgba(0, 172, 193, 1) 100%);
  color: #000;
  max-width: 1400px;
  /* max-height: 500px; */
  border-radius: 15px;
  box-shadow: inset 0px 0px 20px rgba(0, 0, 0, 0.3);
}

.box .subTitle {
  font-size: 36px;
}

.pic {
  border: 1px solid #fff;
  border-radius: 10px;
  margin: 40px;
  max-width: 550px;
  float: left;
  margin-top: 10px;
  margin-bottom: 20px;
}

.text {
  font-size: 36px;
  padding: 20px;
  text-align: justify;
  margin: 20px;
}
